/* src/pages/Contact.css */
.contact-container {
  max-width: 900px;
  margin: 50px auto;
  padding: 30px;
  background-color: #ffe4e1;
  border-radius: 15px;
  box-shadow: 0 4px 15px rgba(255,105,180,0.2);
  text-align: center;
  font-family: 'Arial', sans-serif;
}

.contact-title {
  color: #ff1493;
  font-family: 'Pacifico', cursive;
  font-size: 2.5rem;
  margin-bottom: 10px;
}

.contact-subtitle {
  color: #555;
  font-size: 1.1rem;
  margin-bottom: 25px;
}

.contact-info p {
  margin: 10px 0;
  font-weight: bold;
  color: #ff69b4;
}
