.page-container {
  max-width: 900px;
  margin: 50px auto;
  padding: 20px;
  background-color: #fff0f5; /* very light pink background */
  border-radius: 15px;
  box-shadow: 0 5px 20px rgba(255, 105, 180, 0.2);
  text-align: center;
}

.page-container h1 {
  color: #ff1493; /* hot pink */
  font-family: 'Pacifico';
  font-size: 2.5rem;
  margin-bottom: 20px;
}

.page-container p {
  color: #333;
  font-size: 1.1rem;
  line-height: 1.8;
}

.page-container a {
  color: #ff69b4;
  text-decoration: none;
  font-weight: bold;
}

.page-container a:hover {
  text-decoration: underline;
}
