.book-card {
  background: #fff0f5;
  border-radius: 12px;
  padding: 15px;
  text-align: center;
  box-shadow: 0 4px 10px rgba(255, 105, 180, 0.1);
  transition: transform 0.3s;
}

.book-card:hover {
  transform: translateY(-5px);
}

.book-card img {
  width: 100%;
  height: 220px;
  object-fit: cover;
  border-radius: 8px;
  margin-bottom: 10px;
}

.book-card h3 {
  color: #ff1493;
  margin: 5px 0;
}

.book-card .price {
  font-weight: bold;
  color: #ff69b4;
  margin-bottom: 10px;
}

.book-card button {
  background-color: #ffb6c1;
  color: #fff;
  border: none;
  border-radius: 12px;
  padding: 8px 16px;
  cursor: pointer;
  font-weight: bold;
  transition: 0.3s;
}

.book-card button:hover {
  background-color: #ff1493;
  transform: scale(1.05);
}
