.book-details-container {
  display: flex;
  justify-content: center;
  padding: 30px;
}

.book-details-card {
  display: flex;
  gap: 30px;
  max-width: 900px;
  background-color: #fff;
  padding: 20px;
  border-radius: 12px;
  box-shadow: 0 4px 15px rgba(255,105,180,0.2);
}

.book-image {
  width: 300px;
  border-radius: 8px;
  object-fit: cover;
}

.book-info {
  text-align: left;
}

.book-info h2 {
  color: #ff1493;
  font-family: 'Pacifico';
  margin-bottom: 10px;
}

.book-author {
  color: #555;
  margin-bottom: 10px;
}

.book-price {
  font-weight: bold;
  color: #ff69b4;
  margin-bottom: 10px;
}

.book-description {
  margin-bottom: 20px;
}

.add-to-cart-btn, .back-btn {
  background-color: #ffb6c1;
  color: #fff;
  border: none;
  padding: 8px 16px;
  border-radius: 12px;
  font-weight: bold;
  text-decoration: none;
  cursor: pointer;
  margin-right: 10px;
  display: inline-block;
}

.add-to-cart-btn:hover, .back-btn:hover {
  background-color: #ff1493;
  transform: scale(1.05);
}
/* Quantity input */
.quantity-selector {
  margin: 15px 0;
  display: flex;
  align-items: center;
  gap: 10px;
}

.quantity-selector input {
  width: 60px;
  padding: 5px;
  border-radius: 5px;
  border: 1px solid #ccc;
  text-align: center;
}

/* Popup notification */
.cart-popup {
  position: fixed;
  top: 20px;
  right: 20px;
  background-color: #ff1493; /* hot pink */
  color: #fff;
  padding: 12px 20px;
  border-radius: 10px;
  box-shadow: 0 5px 15px rgba(0,0,0,0.2);
  font-weight: bold;
  z-index: 999;
  animation: fadeInOut 2s forwards;
}

/* Popup animation */
@keyframes fadeInOut {
  0% { opacity: 0; transform: translateY(-20px); }
  10% { opacity: 1; transform: translateY(0); }
  90% { opacity: 1; transform: translateY(0); }
  100% { opacity: 0; transform: translateY(-20px); }
}
