.navbar {
  background-color: #ffb6c1;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 15px;
  box-shadow: 0 2px 5px rgba(0,0,0,0.1);
  position: sticky;
  top: 0;
  z-index: 100;
}

.navbar-title {
  font-family: 'Pacifico';
  color: #ff1493;
  font-size: 2.5rem;
  margin-bottom: 10px;
}

.nav-links {
  display: flex;
  gap: 20px;
  flex-wrap: wrap;
}

.nav-links a {
  color: #fff;
  text-decoration: none;
  font-weight: bold;
  font-size: 16px;
  transition: 0.3s;
}

.nav-links a:hover {
  color: #ff1493;
}
